* {
    padding: 0;
    margin: 0;
    font-family: 'Rubik', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap');

body {
    background-color: #f1f1f1;
    height: auto;
}

.wrapper {
    width: 100%;
}

.header-list {
    display: block;
    height: 20px;
    line-height: 30px;
    flex-wrap: nowrap;
}

.header-list li {
    background-color: #fff;
    display: inline-block;
    height: 24px;
    width: 95px;
    list-style: none;
    border-radius: 10px;
    position: relative;
    text-align: center;
    line-height: 24px;    
}

.header-list li {
    margin-left: 20px;
}  

.header-list li:last-of-type{
    margin-right: 20px;
}

.header {
    background-color: #fff;
    white-space: nowrap;
    border: 1px solid #fff;
    margin-top: 0;
    height: 30px;
    overflow: auto;
    position: relative;
    z-index: 10;
}

main {
    position: relative;
    top: 20px;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #f1f1f1;
}

main .item{
    top: 20px;
    position: relative;
    width: 44%;
    height: 200px;
    background-color: #ffff;
    margin-right: 10px;
    margin-left: 10px;
    margin-top: 10px;
    border-radius: 20px 20px 10px 10px;
    position: relative;
    text-align: justify;
    z-index: 100;
}

main .item img{
    height: 60%;
    background-position: center center;
    background-blend-mode: multiply;
    width: 100%;
    border-radius: 20px 20px 0px 0px;
}

main .item b{
    font-size: 14px;
    padding-left: 2px;
    text-decoration: none;
    font-weight: lighter;
}

main .item p{
    font-size: 15px;
    padding-left: 15px;
    padding-top: 20px;
}

main .item .add-to-cart{
    position: absolute;
    bottom: 10px;
    left:130px;
    background-color: #77db87;
    border: 1px #111 solid;
    width: 18%;
    height: 13%;
    text-align: center;
    line-height: 25px;
    border-radius: 10px;
    transition: all 500ms ease;
    opacity: 100%;
    color: #000;
    border: none;
}

main .item .reduce-item{
    position: absolute;
    bottom: 10px;
    left: 95px;
    background-color: #e65c5c;
    border: 1px #111 solid;
    width: 18%;
    height: 13%;
    text-align: center;
    line-height: 25px;
    border-radius: 10px;
    transition: all 500ms ease;
    opacity: 100%;
    color: #000;
    border: none;
}

main .item .count {
    height: 25px;
    width: 25px;
    background-color: #f9f9f9;
    text-align: center;
    line-height: 25px;
    border-radius: 50%;
    top: 0px;
    right: 0px;
    position:absolute;
    border: 1px solid #000;
}




.full-item .exit-icon{
    position: absolute;
    background: #ca5252;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    top: 2px;
    right: 2px;
    z-index: 100000000;
}

.category{
    position: absolute;
    margin-top: 17px;
    margin-left: 20px;
    font-weight: 500;
    font-size: 25px;
    z-index: 200000;
}

.category-all li:first-of-type,
.category-rolls li:nth-of-type(2),
.category-sushi li:nth-of-type(3),
.category-poke li:nth-of-type(4),
.category-breakfast li:nth-of-type(5),
.category-gunkan li:nth-of-type(6),
.category-soup li:nth-of-type(7),
.category-drinks li:nth-of-type(8){
    background-color: #d1d0d0;
}

.category-default li:first-of-type{
    background-color: #d1d0d0;
    transition: all 500ms ease;
}


.footer {
    font-size: 15px;
    text-align: center;
    position: relative;
    bottom: -50px;
    height: 70px;
}

.full-item{
    background: #000000b8;
    position: fixed;
    top: 0%;
    left: 0%;
    right: 0;
    bottom: 0;
    z-index: 100;
    overflow: auto;
    background-position-y: center;
    overflow-y: hidden;
    z-index: 100;
}



.full-item > div{
    position: fixed;
    top: 30%;
    left: 8%;
    right: 0;
    bottom: 0;
    width: 85%;
    height: 370px;
    border-radius: 10px;
    background: #fff;
    background-position-y: center;
    overflow-y: hidden;
    z-index: 2000000;
}

.full-item  img{
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    transition: transform 500ms ease;
    background: #000000b8;
}

.full-item p:last-of-type{
    font-size: large;
    text-align: center;
}

.full-item h2, .full-item p, .full-item b{
    margin: 10px 20px;
    color: #333;
}


.full-item b{
    color: #5fa36a;
}





.full-item .add-to-cart:hover{
    transform: scale(1.3) ;
}

.full-item .cancel {
    position: absolute;
    top: 5px;
    right: 10px;
    background-color: #ca5252;
    color: #000;
    font-weight: normal;
    height: 30px;
    width: 70px;
    text-align: center;
    line-height: 30px;
    border-radius: 20px;
    transition: all 500ms ease;
}

.cart-default {
    width: 200px;
    z-index: 1000000;
    background-color: #f1f1f1;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: block;
    width: 100px;

}

.cart-opacity {
    width: 100px;
    display: none;
}

.wrapper .main-default {
    display: none;
}

.wrapper .main-opacity {
    display: block;
}



.my-order{
    position: absolute;
    font-size: 20px;
    font-weight: 450;
    top: 20px;
    margin-left: 20px;
    white-space: nowrap;    

}

.edit-order{
    
    margin-top: 20px;
    margin-left: 300px;
    color: #2c8b3c;
    font-weight: 400;
}

.orders .item {
    position: relative;
    top: -25px;
    margin-top: -35px;
}

.orders .item p {
    position: relative;
    top: 8px;
    left: 290px;
    color: #2c8b3c;
}

.orders .item img{
    
    border-radius: 20px;
    position: relative;
    top: 80px;
    left: 20px;
    width: 80%;
}

.orders .item h2{
    position: relative;
    top: 29px;
    left: 110px;
    font-weight: 500;
    font-size: 15px;
}

.orders .item b{
    font-weight: 300;
    font-size: 15px;
    position: relative;
    top: -10px;
    margin-top: 0px;
    margin-left: 320px;
}

.choose-address{
    position: relative;
    font-size: 20px;
    font-weight: 450;
    top: 0px;
    margin-left: 20px;
    white-space: nowrap;    
}


#sushi-here-address{
   position: relative;
   top: 20px;
   left: -20px; 
   color: #2c8b3c;
}

.add-address{
    white-space: nowrap;    
    position: relative;
    top: 10px;
    left: 50px;  
    font-size: 18px;
}


form label{
   position: relative;
   margin-left: 0px;
   text-align: end;
   
}
form {
    
    position: relative;
    left: 70px;
}

